*{
    box-sizing: border-box;
} 
 .app {
  padding: 0;
  inset: 0;
 }
 .app .connect{
  color: rgb(255, 47, 5);
  font-size: 34px;
  border: 1px solid rgb(104, 237, 255);
  background-color: rgb(255, 253, 208);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 10px;
  position:fixed;
  top: 50%;
  left: 50%;
  transform:translate(-50%,-50%);
  /* position: absolute;
  top: 40%;
  left: 45%; */
 }
 .app .connect:hover {
  background-color: rgb(255, 253, 208);
  color: blue;
  cursor: pointer;
 }
 .toppart {
  background-color: #ffffff;
  display: flex;
  justify-content: space-between;
  min-height: 68px;
  align-items: center;
  gap: 8px;
  padding: 8px;
  position: relative;
  box-shadow: 0 2px 8px 0 #0000001a;
}
.modalcontainer {
  width: 357px;
  height: 600px;
  color: #141618;
  margin: 0;
  padding: 0;
  position: absolute;
  /* right: 30px; */
  font-size: 16px;
  box-shadow: 0 1px 2px 2px #0000001a;
  background-color: #ffffff;
}
&.invisible {
  display: none;
}

.category {
    width: 171.42px;
    height: 32px;
    display: flex;
    justify-content: space-between;
    background-color: #0000001a;
    align-items: center;
    gap: 8px;
    padding-left: 8px;
    padding-right: 16px;
    border-radius: 9999px;
    border: none;
}
.icon {
  width: 14px;
  height: 14px;
}
.icon img{
    width: 14px;
    height: 14px;
    max-width: 14px;
    align-items: center;
}
.defaultcategory {
    font-family: "Euclid Circular B","Roboto",sans-serif;
    font-weight: 400;
    font-size: 0.75rem;
    line-height: 1.25rem;
    letter-spacing: 0;
}
.defaultcategory:hover {
  cursor: pointer;;
}
.downicon {
    font-size: 12px;
    width: 1em;
    height: 1em;
    max-width: 1em;
    flex: 0 0 1em;
    display: inline-block;
    margin-left: auto;
}
.logo {
    background-color: transparent;
    border-style: none;
    cursor: pointer;
    height: 38px;
    width: 46px;
}
.logo img {
    height: 32px;
    width: 34px;
}
#mainpart {
  background-color: white;
}
.loading {
  position: absolute;
  top: 0;
  right: 0;
  /* background-color: #f2f4f6; */
  width: 100%;
  height: 600px;
  box-shadow: 0 1px 4px 2px #0000001a;
}
.maincontainer {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: 357px;
    padding: 30px;
    font-weight: 400;
    z-index: 1;
}

/*-------logo container-------*/
#logo-container {
    margin-top: 24px;
    position: relative;
}
#logo-container svg {
  width: 120px;
  height: 120px
}

.maincontainer h1 {
    font-family: "Euclid Circular B","Roboto",sans-serif;
    font-weight: 700;
    font-size: 1.5rem;
    line-height: 2rem;
    letter-spacing: 0;
    color: #6a737d;
    margin-top: 4px;
    margin-bottom: 0px;
}
.maincontainer p {
    margin: 0;
    padding: 0;
    font-family: "Euclid Circular B", Helvetica, Arial, sans-serif;;
    font-size: 16px;
}

form {
    width: 100%;
    margin: 56px 0 8px;
}

.form-group {
  position:relative;  

  & + .form-group {
    margin-top: 30px;
  }
}

.form-label {
  position: absolute;
  left: 0;
  top: 10px;
  color:#6a737d;
  background-color: #fff;
  transition: transform 150ms ease-out, font-size 150ms ease-out;
  font-family: "Roboto", sans-serif;;
  font-size: 16px;
  z-index: 10;
}

.movelabel .form-label {
  transform: translateY(-125%);
  font-size: .75em;
}

.form-input {
  position: relative;
  padding: 12px 0px 5px 0;
  width: 100%;
  outline: 0;
  border: 0;
  box-shadow: 0 1px 0 0 rgba(0, 0, 0, 0.87);
  transition: box-shadow 150ms ease-out;
}

.form-input:focus {
  box-shadow: 0 2px 0 0 #0376c9;
}

.form-input.filled {
  box-shadow: 0 1px 0 0 rgba(0, 0, 0, 0.87);
}

.form p {
  font-size: 0.75rem;
  font-family: "Euclid Circular B", Helvetica, Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  color: #f44336;
  line-height: 140%;
  letter-spacing: 0.03333em;
}
.forgot a:hover{
  color: #026064;
}

.unlocksubmit {
    background-color: #0376c9;
    opacity: 0.5;
    color: #ffffff;
    margin-top: 20px;
    width: 100%;
    height: 50px;
    font-weight: 400;
    box-shadow: none;
    border-radius: 100px;
    border: 1px solid #141618;
    font-size: 0.875rem;
}
.entered {
  opacity: 1;
  cursor: pointer;
}

.forgot {
    font-family: "Euclid Circular B", Helvetica, Arial, sans-serif;
    margin-top: 15px;
    width: 100%;
    text-align: center;
    font-size: 0.75rem; 
    font-weight: bold;
    font-style: normal;
    padding: 0.75rem 1rem;
}
.forgot a {
    color: #0376c9;
    cursor: pointer;
    background-color: transparent;
    line-height: 140%;
}
.help {
    font-family: "Euclid Circular B","Roboto",sans-serif;
    margin-top: 25px;
    font-size: 0.75rem;
}
.help span a {
    color: #0376c9;
    text-decoration: none;
}
.help span a:hover {
    color: #026064;
}

/*--------loading--------*/
.loading {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 357px;
  padding: 30px;
  z-index: 99;
  background-color: #ffffff;
}
.loading-logo {
  width: 160px;
  height: 160px;
  display: flex;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10rem;
}
.loading-spinner {
  width: 2rem;
  height: 2rem;
  display: flex;
  margin: auto;
  margin-top: 1rem;
}